.about {
    &__title {
        font-family: $font-text;
        color: $white;
        text-transform: uppercase;
        text-align: center;
        font-size: 24px;

    }

    &__wrapp {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        @include media ('>=md') {
            flex-wrap: nowrap;
        }
    }

    &__left {
        display: flex;
        flex-wrap: wrap;
        padding-left: Max(15px, calc((100% - 1400px) / 2));
        width: 100%;

        @include media ('>=md') {
            width: 60%;
        }
    }

    &__right {
        @include media ('>=md') {
            width: 40%;
        }
    }


    &__info {
        width: 100%;

        @include media ('>=md') {
            width: 100%;
        }

        @include media ('>=xl') {
            width: 50%;
        }
    }

    &__info-title {
        font-family: $font-text;
        font-size: 24px;
        color: #000;
        margin-bottom: 10px;
        font-weight: 600;
        text-transform: uppercase;
    }

    &__info-text {
        font-family: $font-text;
        font-size: 22px;
        color: #000;
        font-weight: 500;
    }

    &__img {
        width: 100%;

        @include media (">=md") {
            width: 100%;
            ;
            height: 100%;
        }
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;

    }
}

.card {
    border: 1px solid #000;
    background-color: #000;
    box-sizing: border-box;
    padding: 1.25em;
    height: 70px;
    width: 212px;
    margin-right: 60px;
}


// SECTION HOW

.how {
    padding-top: 70px;

    @include media ('>=md') {
        padding-top: 150px;
    }

    &__wrapp {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__black {
        border: 1px solid #000;
        box-sizing: border-box;
        background-color: #000;
        margin-top: 10px;
        width: 212px;
        height: 70px;

        @include media ('>=md') {
            margin-top: 0;
        }
    }

    &__title {
        font-family: $font-text;
        font-size: 24px;
        color: $white;
        font-weight: 600;
        text-transform: uppercase;
        margin-top: 10px;
        text-align: center;
    }

    &__text {
        font-family: $font-text;
        font-size: 22px;
        color: #000;
        font-weight: 500;

        @include media ('>=md') {
            width: 100%;
        }
    }

    &__right {
        padding-left: Max(15px, calc((100% - 1400px) / 2));
        padding-right: Max(15px, calc((100% - 1400px) / 2));

        @include media ('>=md') {
            width: 60%;
        }

        @include media ('>=xl') {
            width: 60%;
        }
    }

    &__left {
        order: 1;

        @include media ('>=md') {
            order: 0;
            width: 40%;
        }

        // @include media ('>=lg') {
        //     width: 40%;
        // }
    }

    &__img {
        width: 100%;
        height: 100%;
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}


