.tabs {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;

    @include media ('>=xl') {
        display: block;
        width: 500px;
    }

    &__title {
        font-family: $font-text;
        font-size: 24px;
        font-weight: 600;
        margin-top: 40px;
        margin-bottom: 40px;
        display: none;

        @include media ('>=xl') {
            display: block;
        }
    }

    &__item {
        margin-bottom: 10px;
        border: 1px solid transparent;
        transition: 0.5s;
        cursor: pointer;

        @include media ('>=md') {
            margin-left: 20px;
        }

        &:hover {
            opacity: 0.5;
        }
    }
}

.works__info-slide {


    display: none;
    // width: 300px;

    @include media ('>=sm') {
        width: 100%;
    }

    @include media ('>=xl') {
        width: 500px;
    }

}

.works__info-slide--show {
    display: block;

}

