.main-menu {
    display: flex;
    margin: 0;
    padding: 0;
    flex-wrap: wrap;
    justify-content: center;

    li {
        list-style: none;
        margin-right: 16px;
    }

    a {
        text-transform: uppercase;
        color: #5E2128;
        position: relative;
        font-family: $font-text;
        font-size: 11px;

        &:hover {
            color: #5E2128;
        }
    }
}

@include media ('>=md') {
    .main-menu {
        a {
            font-size: 24px;
        }
    }
}